import React from 'react';
import "./submit.css";

const Submit = ({formData}) => {
    return (
        <div className="form">
                
                <div className="submitValue">
                <table className="submitTable">
                    <tbody>
                        <tr className="submitTr">
                            <th>Name:</th>
                            <td>{formData.firstName} {formData.lastName} </td>
                        </tr>
                        <tr className="submitTr">
                            <th>Username:</th>
                            <td>{formData.usernaem} </td>
                        </tr>
                        <tr className="submitTr">
                            <th>Email:</th>
                            <td>{formData.email} </td>
                        </tr>
                        <tr className="submitTr">
                            <th>Nationality:</th>
                            <td>{formData.nationality} </td>
                        </tr>
                        <tr className="submitTr">
                            <th>Other:</th>
                            <td>{formData.other}</td>
                        </tr>
                    </tbody>
                </table>
                
            </div>
        </div>
    )
}

export default Submit
